.products__headind-label {

font-family: titan one;
font-style: normal;
font-weight: normal;
font-size: 30px;
line-height: 34px;
text-align: center;
letter-spacing: 0.04em;
text-transform: uppercase;
color: #FFA5BA;
margin-left: auto ;
margin-right: auto;
width: max-content;

}

.extra-text {
	display: none;
}
.extra-text{
	position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
border-radius: 24px;
background: #d4144460;
letter-spacing: 0.03em;

transform: translateY(100%);
transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

}
.background-milkshakes-mini-modal{
    
    background-color: #c2e2973d;
}
.background-ice-coffee-mini-modal{
    background-color: #f0d1a53d;}
#read-more-milkshakes:checked ~ .extra-text {
    display: block;
    transform: translateY(0);
    
}

#read-more-ice-coffee:checked ~ .extra-text {
    display: block;
    transform: translateY(0);
    
}
#read-more:checked ~ .extra-text {
    display: block;
    transform: translateY(0);
    
}
