.ice-coffee::before{
    background-image:  url('../images/product-2m-249@1x.png');
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url('../images/product-2m-498@2x.png');
    
    background-size: 249px 249px;
  }
  @media screen and (min-width: 768px) {
     background-image: url('../images/product-2t-220@1x.png');
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image:url('../images/product-2t-440@2x.png');
      
      background-size: 220px 220px;
    }
  }
  @media screen and (min-width: 1280px) {
     background-image: url('../images/product-2d-335@1x.png');
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image:url('../images/product-2d-670@2x.png');
      background-size: 335px 335px;
      width: 335px;
    }
  } 
    content: '';
    background-position-x: center;
    z-index: 100px;
   position:absolute;
   
    
    margin-bottom: 38px;
    background-repeat: no-repeat;
    @media ( min-width:320px)   {
        width: 280px;
        height: 280px;
       
        margin-top: -150px;
    }
    @media ( min-width:768px)   {
        width: 220px;
        
        margin-top: -150px;
    }
    @media ( min-width:1200px){
        
        margin-top: -200px;
        width: 335px;
    height: 335px;
    margin-left: auto;
    margin-right: auto;
    }



}
.product-menu__item__decoration{
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
