}

.products-menu{
    display: flex;
    flex-wrap: wrap;
    margin-right: 20px;
    @media( min-width:320px) {
        width: 280px;
        
        margin-right: auto;
        margin-left: auto;
    }
    @media( min-width:768px) {
        width: max-content;
        
        margin-right: 0px;
        margin-left: 0px;
        display: flex;
    }
  }
  .products__svg{
      fill: $third-accent-color;
  }

.products-menu__item{
    
        position:relative ;
        overflow: hidden;
        
    /* height: 600px; */
    
    margin-left: 20px;
    @media ( min-width:320px)  {
        width: 280px;
        margin-left: 0px;
    }
    @media ( min-width:768px)  {
        width: 220px;
        margin-left: 20px;
    }
    @media ( min-width:1200px)  {
        width: 330px;
        margin-left: 20px;
    }
}
.products-item__block{
    
    background:#FFB8CA ;
   
    height: 525px;
